.home {
    @apply bg-gradient-to-b from-[#38b2ff] to-[#0073d6] pt-28 pb-32 mobile:pt-5 mobile:pb-0 relative before:bg-[url('/public/images/main-decor-left.svg')] mobile:before:bg-[url('/public/images/main-decor-left-m.svg')] before:w-[279px] mobile:before:w-[80px] before:h-[842px] mobile:before:h-[262px] before:absolute before:top-20 mobile:before:top-[300px] before:left-0 after:bg-[url('/public/images/main-decor-right.svg')] mobile:after:bg-[url('/public/images/main-decor-right-m.svg')] after:w-[292px] mobile:after:w-[120px] after:h-[584px] mobile:after:h-[392px] after:absolute after:top-48 mobile:after:top-[224px] after:right-0;

    .image {
        @apply relative ml-auto mobile:mx-auto drop-shadow-colored;
        will-change: filter;

        & + a {
            @apply mobile:absolute mobile:right-0 mobile:-left-[3px] mobile:bottom-[26px] mobile:max-w-[182px] mobile:mx-auto;
        }
    }
}

.title {
    @apply text-7xl leading-[88px] font-bold font-secondary tracking-tight mobile:text-[22px] mobile:leading-[39px] text-white mobile:mt-5;
}

.subtitle {
    @apply notMobile:text-xl text-white notMobile:leading-[30px] my-5 mobile:my-1;
}

.benefits {
    @apply flex  mobile:mt-[60px] mobile:mx-auto mobile:max-w-[332px] flex-wrap;
}

.benefit {
    @apply w-[208px] max-w-[208px] mobile:max-w-[156px] min-h-[162px] text-center rounded-lg shadow-benefit p-6 whitespace-normal align-middle mobile:w-1/2 mobileXs:w-[208px] mobileXs:mx-auto mobileXs:max-w-[208px] mobile:mb-4 mobileXs:mb-0;

    & + .benefit {
        @apply ml-6 mobileXs:ml-auto mobileXs:mt-4 mobile:ml-0;
    }

    &:nth-child(2n) {
        @apply mobile:ml-auto;
    }

    svg {
        @apply mb-2 mx-auto;
    }

    h4 {
        @apply font-semibold;
    }

    span {
        @apply text-xs inline-block;
    }
}

.advantages {
    @apply -ml-[60px] mobile:ml-0 -mt-[60px] mobile:mt-0 w-[804px] mobile:w-full;
    .benefit {
        @apply ml-[60px] mobile:ml-0 mt-[60px] mobile:mt-4 tablet:ml-auto;

        &:nth-child(2n) {
            @apply mobile:ml-auto;
        }
    }
}

.steps {
    @apply mt-10 mb-9 mobile:mt-[620px] mobile:max-w-[302px] mobile:mx-auto;

    img {
        @apply absolute top-0 -left-[371px] opacity-0 transition-opacity ease-in-out duration-300 z-50 mobile:w-[280px] mobile:h-[562px] mobile:top-14 mobile:left-1/2 mobile:-translate-x-1/2 mobile:right-auto;
        will-change: filter;
        filter: drop-shadow(0px 0px 50px rgba(38, 50, 56, 0.1));

        @media (max-width: 767px) {
            filter: drop-shadow(0px 0px 50px rgba(38, 50, 56, 0.15));
        }
    }

    .overlay {
        @apply relative flex w-[80px] mobile:w-[68px] h-[80px] mobile:h-[68px] min-w-[80px] mobile:min-w-[68px] min-h-[80px] mobile:min-h-[68px] mr-8 mobile:mr-6 rounded-full;
    }

    svg {
        @apply mx-auto my-auto mobile:w-10 mobile:h-10;
    }

    h3 {
        @apply mb-2 text-2xl font-medium mobile:text-xl;
    }
}

.step {
    @apply flex transition-all;

    & + .step {
        @apply mobile:mt-[70px] mt-16;
    }

    &.active {
        .overlay {
            @apply transition ease-in-out bg-[#1E282D] duration-300;
        }

        svg {
            path {
                @apply transition ease-in-out duration-300 fill-white;
            }
        }

        img {
            @apply opacity-100;
        }
    }

    .overlay {
        @apply after:absolute after:-bottom-[87px] after:left-1/2 after:w-[2px] after:h-[78px] mobile:h-[73px] after:bg-[#1E282D] after:rounded-full cursor-pointer;
    }
}

.step {
    &:last-child {
        .overlay {
            &:after {
                content: none;
            }
        }
    }
}
